/* width */
#Ongoing-Orders::-webkit-scrollbar {
    width: 10px;
}

/* Track */
#Ongoing-Orders::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
#Ongoing-Orders::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: .3rem;
}

/* Handle on hover */
#Ongoing-Orders::-webkit-scrollbar-thumb:hover {
    background: #555;
}


/* width */
#Done-Orders::-webkit-scrollbar {
    width: 10px;
}

/* Track */
#Done-Orders::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
#Done-Orders::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: .3rem;
}

/* Handle on hover */
#Done-Orders::-webkit-scrollbar-thumb:hover {
    background: #555;
}

